	module.exports = {"data":{"markdownRemark":{"html":"<p>There are 4 actions you can take on a report that you've submitted:</p>\n<ul>\n<li>Add comment</li>\n<li>Self-close</li>\n<li>Request mediation</li>\n<li>Requesting Public Disclosure</li>\n</ul>\n<h3>Commenting and Closing a Report</h3>\n<p>You can dialogue with the program or triager and make notes about the report through adding comments. Additionally, you can self-close your own report and close it as N/A.</p>\n<p>To add comments or to close a report:</p>\n<ol>\n<li>Go to the bottom of the report you want to take action on.</li>\n<li>In the action picker above the comment box you can choose to <strong>Add comment</strong> or <strong>Close report</strong>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-36767.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 34.64467005076142%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"report-actions-hacker-1\"\n        title=\"\"\n        src=\"/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-2f183.png\"\n        srcset=\"/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-67829.png 125w,\n/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-5c59f.png 250w,\n/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-2f183.png 500w,\n/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-c93e8.png 750w,\n/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-4e628.png 1000w,\n/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-b3ac4.png 1500w,\n/static/report-actions-hacker-1-b2693d51525ae6b38028cbab459cdcaa-36767.png 1576w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n</ol>\n<p>When the report is in the triaged state, you can only add comments to the report.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-f82a3.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 29.770992366412212%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"report-actions-hacker-2\"\n        title=\"\"\n        src=\"/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-2f183.png\"\n        srcset=\"/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-67829.png 125w,\n/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-5c59f.png 250w,\n/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-2f183.png 500w,\n/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-c93e8.png 750w,\n/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-4e628.png 1000w,\n/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-b3ac4.png 1500w,\n/static/report-actions-hacker-2-7c2c7b21aed6ffe465a5129bd42902f0-f82a3.png 1572w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Request Mediation</h3>\n<p>You can also request for mediation from HackerOne in extreme cases when all normal discussions with the team have been attempted and there has been no satisfactory resolution.</p>\n<p>To request mediation, select the <strong>Report abuse</strong> option, and select <strong>Report Mediation</strong>.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/report-actions-hacker-3-8429e0cb12357f3c6cd3668c2642f0a7-2af09.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 10.425844346549193%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAYAAABYBvyLAAAACXBIWXMAAAsSAAALEgHS3X78AAAAbElEQVQI12WMywqEMBAE/f9PdMF1D5kJBEMiZLJ5tqNXD0VD0dTivYcxBveWUhBCgHNOHYGZ8Vc358QY48Xtc85gYv1axBixHBoiIlhrISJPVCQjpaQIaq1orb3ovcNq5LttWNcP9v2HM564AJiXmWAQG73NAAAAAElFTkSuQmCC'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"report-actions-hacker-3\"\n        title=\"\"\n        src=\"/static/report-actions-hacker-3-8429e0cb12357f3c6cd3668c2642f0a7-2f183.png\"\n        srcset=\"/static/report-actions-hacker-3-8429e0cb12357f3c6cd3668c2642f0a7-67829.png 125w,\n/static/report-actions-hacker-3-8429e0cb12357f3c6cd3668c2642f0a7-5c59f.png 250w,\n/static/report-actions-hacker-3-8429e0cb12357f3c6cd3668c2642f0a7-2f183.png 500w,\n/static/report-actions-hacker-3-8429e0cb12357f3c6cd3668c2642f0a7-2af09.png 681w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Request Public Disclosure</h3>\n<p>You can also request public disclosure for your closed reports by selecting <strong>Request public disclosure</strong>. The option is available in your action picker once the report has been closed.</p>\n<p>If the program requests for public disclosure, you'll have the option to <strong>Disclose publicly</strong> as well.</p>\n<blockquote>\n<p><i>Note: Public Disclosure is <strong>Not</strong> available for private programs.</i> </p>\n</blockquote>","frontmatter":{"title":"Report Actions","id":"hackers/report-actions","path":"/hackers/report-actions.html","description":null}}},"pathContext":{}}

